@import url(https://fonts.googleapis.com/css2?family=Cormorant+SC:wght@300&family=Courgette&family=MonteCarlo&family=Permanent+Marker&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


ul{
    display: flex;
    justify-content:flex-end;
    color: #ff0000;
    list-style-type: none;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
    margin: 0;
    
   
}
nav li{
    margin: 10px;
    font-size: 0.8em;
    font-size: 25px;
   
}
.link{
   text-decoration: none;
    color:#FF6060;
    font-weight: 500;
   
}

.activated{
    text-decoration: underline;
}

a{
    text-decoration: none;
}
@media screen and (max-width: 699px){
    nav li{
        font-size: 12px;
    }
}
.container{
    display: flex;
    justify-content: space-between;
    align-self: baseline;
    margin: 20px;
    width: 90%;
}

.container img{
    height: 50px;
    width: 180px;
}
@media screen and (max-width: 699px){

    .container{
        margin: 20px 30px;
    }
    
    .container img{
        height: 30px;
        width: 70px;
    }
  
}
.footer-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: black;
    color: white;
    justify-content: space-between;
    align-items: center;
    padding-top: 25px;
    height: 100px;
}
.footer-container img{
    width: 100px;
}
.footer-container p{
    color: white;
}
span{
    background-color: black;
    width: 40px;
    
    border-radius: 50%;
    color: white;
    margin-right: 5px;
    border: 1px solid white;
}

body{
    margin: 0;
    margin: 0 50px;
}
.container{
    margin: 20px 50px;
}
.cover img{
   border-radius: 25px;
   height: 250px;
   
}
.desktop{
    display: block;
}


@media screen and (max-width: 699px){
    
    body{
        margin: 0 10px;
    }
    .container{
        margin: 20px 10px;
    }
    .cover img{
        height: 250px;
    }
    .Mobile{
        display: block;
    }
    
    .about-block{
        margin: 0 30px;
    }

  
}
.collapse-block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #ff6060;
  margin: 0 auto;
  margin-bottom: 10px;
  max-width: 900px;
  color: #f6f6f6;
  border-radius: 10px;
  padding: 10px 20px;
}

button {
  width: 30px;
  height: 30px;
  background: transparent;
  border: 0;
}
.vector {
  height: 30px;
  width: 30px;
  rotate: 180deg;
  cursor: pointer;
}
.title {
  position: absolute;
}

h1 {
  color: white;
  -webkit-margin-after: 0;
          margin-block-end: 0;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  font-size: 20px;
}

.texte {
  height: -webkit-max-content;
  height: max-content;
  background-color: #f6f6f6;
  font-size: 1.3em;
  padding: 10px 20px;
  position: relative;
  top: -20px;
  max-width: 900px;
  margin: 0 auto;
}


.cover{
    display: flex;
    justify-content: center;
    margin: 20px 0;
}

.imgAccueil{
    width: 100%;
    height: 180px;
    -webkit-filter: contrast(0.5);
            filter: contrast(0.5);
    border-radius: 15px;
   
}
.cover p{
    font-family: 'montserrat';
    position: absolute;
    color: white;
    font-size: 34px;
    -webkit-transform: translate(10px, 50px);
            transform: translate(10px, 50px);
    z-index: 6;
}
.listCard{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content:center;
   max-width: 1200px;
   background-color:#F6F6F6;
   margin: 0 30px;
   border-radius: 15px;
    
}
.card{
    display: flex;
    flex-direction: column-reverse;
    height: 240px;
    width: 260px;
    margin: 10px 20px;
    border-radius: 15px;
    background: rgb(255,96,96);
    background: linear-gradient(180deg, rgba(255,96,96,1) 35%, rgba(13,9,12,0.5788690476190477) 100%);  
}
.card img{
    width: 100%;
    height: 100%;
    border-radius: 15px;
    object-fit: cover;
    -webkit-filter: brightness(0.8);
            filter: brightness(0.8);
}
.card b{
    position: absolute;
    margin: 10px;
    color: white;
    font-size: 18px;
    width: 20%;
    display: flex;
    flex-wrap: wrap;
    

}

@media screen and (max-width: 700px){

    .imgAccueil{
        height: 100px;
    
    }
    .listCard{
        background-color: white;
        margin:0 0;
    }
    .card{
        height: 100%;
         width: 100%;
         margin: 10px 0px;
    }
    .card b{
        width: 40%;
    }
    
    .cover{
        justify-content: flex-start;

    }
    .cover p{
       
        font-size: 25px;
    width: 50%;
    }
}
