}
.slider{
    display: flex;
    flex-direction: column;
    align-content: center;
    position: relative;
   
}

.slider img{
    border-radius: 25px;
}


.vectorNext{
    height: 30px;
    width: 30px;
    position: absolute;
    rotate: 90deg;
    cursor: pointer;
    top: 50%;
    -webkit-transform: translateX(50%);
            transform: translateX(50%);
    right: 0;
    
}
.vectorPrev{
    height: 30px;
    width: 30px;
    position: absolute;
    rotate: -90deg;
    margin-left: 0px;
    align-self: flex-start;
    cursor: pointer;
    top: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

.image-number{
    font-size: 15px;
    color: white;
    position: absolute;
    left: 50%;
    bottom: 20px;

}

.bodyError{
    background-color: #E5E5E5;
}   

.blocError {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.blocError p{
    font-size: 36px;
    padding: 0 20px;
}
strong {
    font-size: 288px;
    font-weight: 700;
    margin-bottom: 20px;
    color: #FF6060;

}
.bodyError p{
    font-size: 36px;
}
.blocError a {
    padding: 70px;
    font-size: 18px;
    text-decoration: underline;
}

@media screen and (max-width: 829px){
    strong{
        font-size: 96px;
    }
    .blocError p{
        font-size: 18px;
        text-align: center;
    }
    .blocError a{
        font-size: 14px;
    }
}
